<template>
  <div class="goods">
      <options :data="dataOption" v-on:change="optchange" ref="goodsOpt"/>
      <tables :data="dataTable" :list="tableList" v-on:change="tabchange"/>
      <!-- 分页 -->
      <div class="demonstration-box">
        <el-pagination @size-change="handleSizeChange"
          :current-page.sync="currentPage"
          :total="pageParams.totalCount"
          :page-size="pageParams.size" 
          layout="total, prev, pager, next" 
        ></el-pagination>
      </div>
      <!-- 删除提示 -->
      <el-dialog title="编辑" :visible.sync="dialogVisible" width="30%">
        <span>{{dialogContent}}</span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="sure">确 定</el-button>
        </span>
      </el-dialog>
      <!-- 编辑提示 -->
      <el-dialog title="提示" :visible.sync="editDialog" width="30%">
        <div class="item edit-dialog">
          <span>订单状态：</span>
          <el-select v-model="state" placeholder="全部">
              <el-option
                  v-for="item in orderDialogList"
                  :key="item.id"
                  :label="item.dValue"
                  :value="item.id"
              ></el-option>
          </el-select>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialog = false">取 消</el-button>
          <el-button type="primary" @click="editSure">确 定</el-button>
        </span>
      </el-dialog>
  </div>
</template>

<script>
import Options from 'views/common/options'
import Tables from 'views/common/tables'
import { getOrderState, getSchool, getClass, getGoods, filterClass, getUsersAccount } from 'data/listBody'
import { DateDay, ScondDay } from "utils/formData";
import queryString from "querystring"
import page from 'data/page'

const dataOption = {
  leftMenu: [
    // { id:'1', name:'订单编号：', placeholder:'请输入订单编号', module:'name'},
    { id:'2', name:'商品名称：', placeholder:'请输入商品名称', module:'goodsId', type:'select'},
    // { id:'3', name:'商品数量：', placeholder:'请输入商品数量', module:'price'},
    { id:'4', name:'校区：', placeholder:'请选择校区名称', module:'schoolId', type:'select'},
    { id:'5', name:'班级：', placeholder:'请选择班级名称', module:'classId', type:'select'},
    // { id:'6', name:'带班老师：', placeholder:'请选择带班老师', module:'price'},
    { id:'7', name:'学员：', placeholder:'请选择学员', module:'userId', type:'select' },
    // { id:'8', name:'订单时间：', placeholder:'请选择订单时间', module:'beginTime', type:'date'},
    { id:'9', name:'订单状态：', placeholder:'请选择订单状态', module:'state', type:'select'},
  ],
  rightMenu: [
    { id:'r1', name:'查询'}
  ]
}

const dataTable = {
  table: [
    // { label:'订单编号', prop:'name'},
    { label:'商品名称', prop:'goodsName'},
    { label:'校区', prop:'schoolName'},
    { label:'班级', prop:'className'},
    { label:'带班老师', prop:'teacherName'},
    { label:'学员', prop:'userName'},
    { label:'订单状态', prop:'stateName'},
    { label:'数量', prop:'goodsCount'},
    { label:'时间', prop:'time'},
  ],
  edit: [
    {name: '编辑', id: 't1'},
    {name: '删除', id: 't2'},
  ]
}

const tips = ['确认删除该条信息?'];

export default {
  name: 'orders',
  mixins: [page],
  data() {
    return {
        dataOption: dataOption,
        dataTable: dataTable,
        dialogVisible: false,
        dialogContent: tips[0],
        editDialog: false,
        tableList: [],
        orderList: [], //订单状态列表
        orderDialogList: [], //订单状态弹框列表
        state: '', //订单状态切换
        loading: false,
        currentPage: 1,
        id: ''
    }
  },
  components: {
    Options,
    Tables
  },
  mounted(){
    this.search();
    this.dropInit();
  },
  activated(){
    this.search();
  },
  methods:{
    dropInit() {
        getGoods(res => {
            let { leftMenu } = this.dataOption;
            leftMenu = leftMenu.map(item => {
                if (item.module === "goodsId") item.options = res;
                return item;
            });
            this.dataOption.leftMenu = leftMenu;
        });
        getSchool(res => {
            let { leftMenu } = this.dataOption;
            leftMenu = leftMenu.map(item => {
                if (item.module === "schoolId") item.options = res;
                return item;
            });
            this.dataOption.leftMenu = leftMenu;
        });
        getUsersAccount(res => {
            let { leftMenu } = this.dataOption;
            leftMenu = leftMenu.map(item => {
                if (item.module === "userId") item.options = res;
                return item;
            });
            this.dataOption.leftMenu = leftMenu;
        });
        getOrderState(res => {
            this.orderList = res;
            let { leftMenu } = this.dataOption;
            leftMenu = leftMenu.map(item => {
                if (item.module === "state") item.options = res;
                return item;
            });
            this.dataOption.leftMenu = leftMenu;
        });        
        getOrderState(res => {
            this.orderDialogList = res;
        },'filter');
    },
    optchange(params){
        console.log(params.id + '--id被单击了')
        if(params.id === 'r1') this.search();
    },
    tabchange(params){
        console.log(params.id + '--id被单击了')
        this.id = params.data.id;

        // 编辑
        if(params.id === 't1') return this.editDialog = true;
        // 删除
        if(params.id === 't2') return this.dialogVisible = true;
    },
    editSure(){
        const params = {
            id: this.id,
            state: this.state,
        };
        this.loading = true;
        this.api.put(`/order`, params, "form").then(res => {
            this.search();            
            this.loading = false;
            this.editDialog = false;
        })
        .catch(err => {
            this.loading = false;
            this.editDialog = false;
            console.log(err);
            this.$message(err.message);
        });
    },
    sure(){
        this.api.delete(`/order/${this.id}`).then((res)=>{
            this.loading = false;
            this.search();
            this.$message(res.data);     
        }).catch((err) => {
            console.log(err)
            this.$message(err.data.message)
        })
        this.dialogVisible = false;
    },
    search(){  //搜索
        const optParams = this.$refs.goodsOpt.getData();
        const pageParams = { size: 20, page: this.currentPage -1 };
        const params = Object.assign({}, optParams, pageParams);
        const param = queryString.stringify(params || {});

        this.loading = true;
        this.api.get(`/order?${param}`).then((res)=>{
            this.loading = false;
            this.dataInit(res.data);
            this.setPage(res.data);
            this.tableList = res.data.list;
        }).catch((err) => {
            console.log(err)
            this.$message(err.data.message)
        })
    },
    dataInit(data){//数据初始化
        const tableList = data.list.map(item => {
            if(item.createTime) item.time = ScondDay(item.createTime);            
            let itemArr = this.orderList.filter(obj => {
                return obj.id === item.state;
            })
            if(itemArr.length < 1) return;
            item.stateName = itemArr[0].dValue;
        })
        this.tableList = tableList;
    }
  }
}
</script>
 
<style lang="less" scoped>

</style>